import React, { useState, useEffect } from 'react';
import {
  Container,
  Grid,
  Typography,
  makeStyles,
  Paper,
  IconButton,
  Card,
  CardMedia,
  CardContent,
  CardActions,
  Button,
} from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import { useHistory } from 'react-router-dom';
import { useAuth } from '../../context/AuthContext';

const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {
    paddingTop: '56.25%', // 16:9
    position: 'relative',
  },
  cardContent: {
    flexGrow: 1,
  },
  price: {
    color: theme.palette.primary.main,
    fontWeight: 600,
    fontSize: '1.25rem',
    marginTop: theme.spacing(1),
  },
  deleteButton: {
    marginLeft: 'auto',
  },
  noFavorites: {
    textAlign: 'center',
    padding: theme.spacing(4),
  },
}));

function FavoritesList() {
  const classes = useStyles();
  const history = useHistory();
  const { user } = useAuth();
  const [favorites, setFavorites] = useState([]);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState('');

  useEffect(() => {
    fetchFavorites();
  }, []);

  const fetchFavorites = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/favorites', {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });
      const data = await response.json();
      setFavorites(data);
    } catch (error) {
      console.error('Error fetching favorites:', error);
      setMessage('获取收藏失败');
    } finally {
      setLoading(false);
    }
  };

  const handleRemoveFavorite = async (itemId) => {
    try {
      const response = await fetch(`http://localhost:5000/api/favorites/${itemId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });

      if (response.ok) {
        setFavorites(favorites.filter(item => item._id !== itemId));
        setMessage('已取消收藏');
      }
    } catch (error) {
      console.error('Error removing favorite:', error);
      setMessage('取消收藏失败');
    }
  };

  const handleItemClick = (itemId) => {
    history.push(`/items/${itemId}`);
  };

  if (loading) {
    return (
      <Container className={classes.container}>
        <Typography>加载中...</Typography>
      </Container>
    );
  }

  if (!user) {
    return (
      <Container className={classes.container}>
        <Paper className={classes.noFavorites}>
          <Typography variant="h6">请先登录</Typography>
        </Paper>
      </Container>
    );
  }

  if (favorites.length === 0) {
    return (
      <Container className={classes.container}>
        <Paper className={classes.noFavorites}>
          <Typography variant="h6">暂无收藏商品</Typography>
          <Button 
            color="primary" 
            onClick={() => history.push('/')}
            style={{ marginTop: 16 }}
          >
            去逛逛
          </Button>
        </Paper>
      </Container>
    );
  }

  return (
    <Container className={classes.container}>
      <Typography variant="h4" gutterBottom>
        我的收藏
      </Typography>
      <Grid container spacing={4}>
        {favorites.map((item) => (
          <Grid item key={item._id} xs={12} sm={6} md={4}>
            <Card className={classes.card}>
              <CardMedia
                className={classes.cardMedia}
                image={`http://localhost:5000${item.image}`}
                title={item.title}
                onClick={() => handleItemClick(item._id)}
                style={{ cursor: 'pointer' }}
              />
              <CardContent className={classes.cardContent}>
                <Typography gutterBottom variant="h6" component="h2">
                  {item.title}
                </Typography>
                <Typography className={classes.price}>
                  ¥ {item.price?.toLocaleString()}
                </Typography>
              </CardContent>
              <CardActions>
                <IconButton
                  className={classes.deleteButton}
                  onClick={() => handleRemoveFavorite(item._id)}
                >
                  <DeleteIcon />
                </IconButton>
              </CardActions>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Container>
  );
}

export default FavoritesList;