import React, { useState, useEffect } from 'react';
import {
  Container,
  Grid,
  Typography,
  Card,
  CardMedia,
  CardContent,
  makeStyles,
  Box,
  CircularProgress,
  Chip,
  Avatar,
  Divider,
} from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import LocalOfferIcon from '@material-ui/icons/LocalOffer';
import TimelapseIcon from '@material-ui/icons/Timelapse';
import WhatshotIcon from '@material-ui/icons/Whatshot';

const formatTimeAgo = (date) => {
  const seconds = Math.floor((new Date() - new Date(date)) / 1000);

  let interval = seconds / 31536000;
  if (interval > 1) return Math.floor(interval) + ' 年前';

  interval = seconds / 2592000;
  if (interval > 1) return Math.floor(interval) + ' 个月前';

  interval = seconds / 86400;
  if (interval > 1) return Math.floor(interval) + ' 天前';

  interval = seconds / 3600;
  if (interval > 1) return Math.floor(interval) + ' 小时前';

  interval = seconds / 60;
  if (interval > 1) return Math.floor(interval) + ' 分钟前';

  return '刚刚';
};

const useStyles = makeStyles((theme) => ({
    container: {
      paddingTop: theme.spacing(4),
      paddingBottom: theme.spacing(4),
    },
    sectionTitle: {
      fontWeight: 600,
      marginBottom: theme.spacing(3),
      display: 'flex',
      alignItems: 'center',
      gap: theme.spacing(1),
      '& svg': {
        color: theme.palette.primary.main,
      },
    },
    card: {
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
      cursor: 'pointer',
      transition: 'all 0.3s ease',
      borderRadius: theme.spacing(1.5),
      boxShadow: '0 2px 8px rgba(0,0,0,0.08)',
      '&:hover': {
        transform: 'translateY(-8px)',
        boxShadow: '0 12px 20px rgba(0,0,0,0.12)',
      },
    },
    cardMedia: {
      height: 220,
      backgroundSize: 'cover',
      borderRadius: `${theme.spacing(1.5)}px ${theme.spacing(1.5)}px 0 0`,
    },
    cardContent: {
      flexGrow: 1,
      padding: theme.spacing(2),
    },
    price: {
      color: theme.palette.error.main,
      fontWeight: 700,
      fontSize: '1.25rem',
      display: 'flex',
      alignItems: 'center',
      gap: theme.spacing(0.5),
      marginTop: theme.spacing(1),
    },
    priceIcon: {
      fontSize: '1rem',
      color: theme.palette.error.light,
    },
    sellerInfo: {
      display: 'flex',
      alignItems: 'center',
      padding: theme.spacing(2),
      backgroundColor: theme.palette.background.default,
      borderRadius: `${theme.spacing(1.5)}px ${theme.spacing(1.5)}px 0 0`,
    },
    avatar: {
      width: 32,
      height: 32,
      marginRight: theme.spacing(1),
      border: `2px solid ${theme.palette.background.paper}`,
    },
    username: {
      fontSize: '0.9rem',
      fontWeight: 500,
      color: theme.palette.text.primary,
    },
    timeChip: {
      marginLeft: 'auto',
      backgroundColor: theme.palette.background.paper,
      fontSize: '0.75rem',
    },
    title: {
      fontSize: '1rem',
      fontWeight: 500,
      marginBottom: theme.spacing(1),
      display: '-webkit-box',
      '-webkit-line-clamp': 2,
      '-webkit-box-orient': 'vertical',
      overflow: 'hidden',
      textOverflow: 'ellipsis',
    },
    description: {
      color: theme.palette.text.secondary,
      fontSize: '0.875rem',
      marginBottom: theme.spacing(1),
      display: '-webkit-box',
      '-webkit-line-clamp': 2,
      '-webkit-box-orient': 'vertical',
      overflow: 'hidden',
    },
    loadingContainer: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      minHeight: 400,
    },
    divider: {
      margin: theme.spacing(6, 0),
    },
    emptyMessage: {
      textAlign: 'center',
      padding: theme.spacing(4),
      backgroundColor: theme.palette.background.paper,
      borderRadius: theme.spacing(2),
    },
  }));

  function DiscoverPage() {
    const classes = useStyles();
    const history = useHistory();
    const [newItems, setNewItems] = useState([]);
    const [loading, setLoading] = useState(true);
  
    useEffect(() => {
      fetchItems();
      const interval = setInterval(fetchItems, 60000);
      return () => clearInterval(interval);
    }, []);
  
    const fetchItems = async () => {
      try {
        setLoading(true);
        const response = await fetch('http://localhost:5000/api/items', {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        });
        
        const data = await response.json();
        
        // 过滤出一小时内发布的商品
        const oneHourAgo = new Date(Date.now() - 60 * 60 * 1000);
        const recentItems = data
          .filter(item => new Date(item.createdAt) > oneHourAgo)
          .sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
        
        setNewItems(recentItems);
      } catch (error) {
        console.error('Error:', error);
        setNewItems([]);
      } finally {
        setLoading(false);
      }
    };
  
    const handleItemClick = (itemId) => {
      history.push(`/items/${itemId}`);
    };
  
    if (loading) {
      return (
        <Container className={classes.loadingContainer}>
          <CircularProgress />
        </Container>
      );
    }

  return (
    <Container className={classes.container}>
      <Box mb={4}>
        <Typography variant="h5" className={classes.sectionTitle}>
          <TimelapseIcon />
          最新上架
          <Chip 
            size="small" 
            label="1小时内" 
            color="primary" 
            style={{ marginLeft: 8 }}
          />
        </Typography>
        
        {newItems.length > 0 ? (
          <Grid container spacing={3}>
            {newItems.map((item) => (
              <Grid item key={item._id} xs={12} sm={6} md={4} lg={3}>
                <Card 
                  className={classes.card}
                  onClick={() => handleItemClick(item._id)}
                >
                  <Box className={classes.sellerInfo}>
                    <Avatar 
                      src={item.seller && item.seller.avatar} 
                      className={classes.avatar}
                    >
                      {item.seller && item.seller.username && item.seller.username[0]}
                    </Avatar>
                    <Typography className={classes.username}>
                      {item.seller && item.seller.username}
                    </Typography>
                    <Chip
                      size="small"
                      className={classes.timeChip}
                      label={formatTimeAgo(item.createdAt)}
                    />
                  </Box>
                  <CardMedia
                    className={classes.cardMedia}
                    image={item.image ? `http://localhost:5000${item.image}` : '/default-item-image.jpg'}
                    title={item.title}
                  />
                  <CardContent className={classes.cardContent}>
                    <Typography className={classes.title}>
                      {item.title}
                    </Typography>
                    {item.description && (
                      <Typography className={classes.description}>
                        {item.description}
                      </Typography>
                    )}
                    <Typography className={classes.price}>
                      <LocalOfferIcon className={classes.priceIcon} />
                      ¥ {item.price && item.price.toLocaleString()}
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        ) : (
          <Box className={classes.emptyMessage}>
            <Typography variant="h6" gutterBottom>
              暂无最新商品
            </Typography>
            <Typography color="textSecondary">
              稍后再来看看吧！
            </Typography>
          </Box>
        )}
      </Box>

      <Divider className={classes.divider} />
    </Container>
  );
}

export default DiscoverPage;