import React, { useState, useEffect } from 'react';
import {
  Container,
  Paper,
  Typography,
  Grid,
  Card,
  CardMedia,
  CardContent,
  IconButton,
  Button,
  makeStyles,
  Box,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Snackbar,
} from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import { useAuth } from '../../context/AuthContext';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  card: {
    display: 'flex',
    marginBottom: theme.spacing(2),
  },
  cardMedia: {
    width: 150,
  },
  cardContent: {
    flex: 1,
  },
  totalSection: {
    marginTop: theme.spacing(3),
    padding: theme.spacing(2),
  },
  deleteButton: {
    marginLeft: 'auto',
  },
  checkoutButton: {
    marginTop: theme.spacing(2),
    padding: theme.spacing(1.5),
    borderRadius: 25,
  },
  dialogContent: {
    padding: theme.spacing(3),
  },
  dialogPrice: {
    color: theme.palette.primary.main,
    fontWeight: 600,
    fontSize: '1.5rem',
    marginTop: theme.spacing(2),
  },
}));

function Cart() {
  const classes = useStyles();
  const { user } = useAuth();
  const history = useHistory();
  const [cartItems, setCartItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState('');
  const [openCheckoutDialog, setOpenCheckoutDialog] = useState(false);

  useEffect(() => {
    fetchCartItems();
  }, []);

  const fetchCartItems = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/cart', {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });
      const data = await response.json();
      setCartItems(data);
    } catch (error) {
      console.error('Error fetching cart items:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleRemoveFromCart = async (itemId) => {
    try {
      const response = await fetch(`http://localhost:5000/api/cart/${itemId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });

      if (response.ok) {
        setCartItems(cartItems.filter(item => item._id !== itemId));
      }
    } catch (error) {
      console.error('Error removing item from cart:', error);
    }
  };

  const handleCheckout = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/transactions/purchase-cart', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });

      const data = await response.json();

      if (response.ok) {
        setMessage('购买成功！感谢您的购买，祝您购物愉快！');
        setOpenCheckoutDialog(false);
        setTimeout(() => {
          history.push('/profile');
        }, 2000);
      } else {
        setMessage(data.message || '购买失败');
      }
    } catch (error) {
      console.error('Checkout error:', error);
      setMessage('购买失败，请稍后重试');
    }
  };

  const calculateTotal = () => {
    return cartItems.reduce((total, item) => total + item.price, 0);
  };

  return (
    <Container className={classes.container}>
      <Typography variant="h4" gutterBottom>
        购物车
      </Typography>

      {loading ? (
        <Typography>加载中...</Typography>
      ) : cartItems.length === 0 ? (
        <Typography>购物车是空的</Typography>
      ) : (
        <>
          {cartItems.map((item) => (
            <Card key={item._id} className={classes.card}>
              <CardMedia
                className={classes.cardMedia}
                image={`http://localhost:5000${item.image}`}
                title={item.title}
              />
              <CardContent className={classes.cardContent}>
                <Typography variant="h6">{item.title}</Typography>
                <Typography variant="h6" color="primary">
                  ¥{item.price}
                </Typography>
              </CardContent>
              <IconButton
                className={classes.deleteButton}
                onClick={() => handleRemoveFromCart(item._id)}
              >
                <DeleteIcon />
              </IconButton>
            </Card>
          ))}

          <Paper className={classes.totalSection}>
            <Typography variant="h6">
              总计: ¥{calculateTotal()}
            </Typography>
            <Button
              variant="contained"
              color="primary"
              fullWidth
              className={classes.checkoutButton}
              onClick={() => setOpenCheckoutDialog(true)}
              disabled={user?.balance < calculateTotal()}
            >
              {user?.balance < calculateTotal() ? '余额不足' : '结算'}
            </Button>
          </Paper>

          <Dialog 
            open={openCheckoutDialog} 
            onClose={() => setOpenCheckoutDialog(false)}
            maxWidth="sm"
            fullWidth
          >
            <DialogTitle>确认购买</DialogTitle>
            <DialogContent className={classes.dialogContent}>
              <Typography variant="h6" gutterBottom>
                购物车商品 ({cartItems.length})
              </Typography>
              <Typography className={classes.dialogPrice}>
                总计: ¥{calculateTotal()}
              </Typography>
              <Typography color="textSecondary">
                购买后余额: ¥{(user?.balance - calculateTotal())?.toLocaleString()}
              </Typography>
            </DialogContent>
            <DialogActions>
              <Button 
                onClick={() => setOpenCheckoutDialog(false)} 
                color="primary"
              >
                取消
              </Button>
              <Button
                onClick={handleCheckout}
                color="primary"
                variant="contained"
              >
                确认购买
              </Button>
            </DialogActions>
          </Dialog>

          <Snackbar
            open={!!message}
            autoHideDuration={2000}
            onClose={() => setMessage('')}
            message={message}
            anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
          />
        </>
      )}
    </Container>
  );
}

export default Cart;