import React, { useState, useEffect } from 'react';
import { 
  Container, 
  Grid, 
  Typography, 
  makeStyles, 
  Paper,
  InputBase,
  Button,
  Chip,
  Card,
  CardMedia,
  CardContent,
  CardActions,
  IconButton,
  Box,
  Slider,
  FormControl,
  Select,
  MenuItem,
  Divider,
  CircularProgress,
  TextField,
} from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import SearchIcon from '@material-ui/icons/Search';
import FavoriteIcon from '@material-ui/icons/FavoriteBorder';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import FilterListIcon from '@material-ui/icons/FilterList';
import SortIcon from '@material-ui/icons/Sort';
import { useAuth } from '../../context/AuthContext';
const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: '#f5f7fa',
    minHeight: '100vh',
    paddingBottom: theme.spacing(4),
  },
  banner: {
    width: '100%',
    height: 400,
    position: 'relative',
    backgroundColor: '#ffffff',
    overflow: 'hidden',
    display: 'flex',
    backgroundImage: `url(${process.env.PUBLIC_URL}/11.png)`,
    alignItems: 'center',
    justifyContent: 'center',
    marginBottom: theme.spacing(4),
  },
  bannerContent: {
    textAlign: 'center',
    zIndex: 1,
    marginTop: -120,
  },
  searchContainer: {
    padding: theme.spacing(2),
    marginBottom: theme.spacing(3),
    backgroundColor: '#ffffff',
    borderRadius: theme.shape.borderRadius,
    boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
  },
  search: {
    display: 'flex',
    alignItems: 'center',
    backgroundColor: '#f5f5f5',
    borderRadius: theme.shape.borderRadius,
    padding: theme.spacing(1, 2),
  },
  searchInput: {
    flex: 1,
    marginLeft: theme.spacing(1),
    fontSize: '1rem',
  },
  filterSection: {
    padding: theme.spacing(2, 0),
    display: 'flex',
    alignItems: 'center',
    flexWrap: 'wrap',
    gap: theme.spacing(1),
  },
  filterChip: {
    margin: theme.spacing(0.5),
  },
  itemCard: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    transition: 'all 0.3s ease',
    '&:hover': {
      transform: 'translateY(-4px)',
      boxShadow: '0 4px 20px rgba(0,0,0,0.1)',
    },
  },
  itemMedia: {
    height: 200,
    position: 'relative',
  },
  itemContent: {
    flexGrow: 1,
    padding: theme.spacing(2),
  },
  cardActions: {
    padding: theme.spacing(2),
    borderTop: '1px solid #f0f0f0',
    justifyContent: 'space-between',
  },
  price: {
    color: theme.palette.primary.main,
    fontWeight: 600,
    fontSize: '1.25rem',
    marginTop: theme.spacing(1),
  },
  buyButton: {
    borderRadius: 20,
    textTransform: 'none',
    fontWeight: 500,
    padding: theme.spacing(1, 3),
  },
  loadingContainer: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: 400,
  },
  sortSelect: {
    marginLeft: 'auto',
    minWidth: 150,
  },
  categoryTitle: {
    marginBottom: theme.spacing(2),
    fontWeight: 600,
  },
  noItems: {
    textAlign: 'center',
    padding: theme.spacing(4),
  }
}));

const categories = [
  '全部',
  '电子产品',
  '服装',
  '图书',
  '家居',
  '运动',
  '其他'
];

function Home() {
  const classes = useStyles();
  const history = useHistory();
  const { user } = useAuth();
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('全部');
  const [sortBy, setSortBy] = useState('newest');
  const [error, setError] = useState('');

  useEffect(() => {
    fetchItems();
  }, [selectedCategory, sortBy]);

  const fetchItems = async () => {
    try {
      setLoading(true);
      const response = await fetch('http://localhost:5000/api/items');
      if (!response.ok) {
        throw new Error('获取商品列表失败');
      }
      const data = await response.json();
      
      // 根据分类筛选
      let filteredItems = selectedCategory === '全部' 
        ? data 
        : data.filter(item => item.category === selectedCategory);

      // 根据搜索词筛选
      if (searchTerm) {
        filteredItems = filteredItems.filter(item => 
          item.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
          item.description.toLowerCase().includes(searchTerm.toLowerCase())
        );
      }

      // 排序
      filteredItems.sort((a, b) => {
        switch (sortBy) {
          case 'priceAsc':
            return a.price - b.price;
          case 'priceDesc':
            return b.price - a.price;
          case 'newest':
          default:
            return new Date(b.createdAt) - new Date(a.createdAt);
        }
      });

      setItems(filteredItems);
      setError('');
    } catch (err) {
      console.error('Error fetching items:', err);
      setError('获取商品列表失败，请稍后重试');
    } finally {
      setLoading(false);
    }
  };

  const handleSearch = (event) => {
    event.preventDefault();
    fetchItems();
  };

  const handleViewItem = (itemId) => {
    history.push(`/items/${itemId}`);
  };

  if (error) {
    return (
      <Container>
        <Typography color="error" align="center">
          {error}
        </Typography>
      </Container>
    );
  }

  return (
    <div className={classes.root}>
      <div className={classes.banner}>
        
        <div className={classes.bannerContent}>
          <Typography variant="h3" gutterBottom>
            发现好物
          </Typography>
          <Typography variant="h6" color="textSecondary">
            二手交易，让闲置焕发新生
          </Typography>
        </div>
      </div>

      <Container>
        <Paper className={classes.searchContainer}>
          <form onSubmit={handleSearch}>
            <div className={classes.search}>
              <SearchIcon color="action" />
              <InputBase
                className={classes.searchInput}
                placeholder="搜索商品..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
              <Button 
                type="submit"
                variant="contained" 
                color="primary"
                className={classes.buyButton}
              >
                搜索
              </Button>
            </div>
          </form>

          <div className={classes.filterSection}>
            <Box display="flex" alignItems="center" flexWrap="wrap" flex={1}>
              {categories.map((category) => (
                <Chip
                  key={category}
                  label={category}
                  onClick={() => setSelectedCategory(category)}
                  className={classes.filterChip}
                  color={selectedCategory === category ? "primary" : "default"}
                  variant={selectedCategory === category ? "default" : "outlined"}
                />
              ))}
            </Box>

            <FormControl variant="outlined" size="small" className={classes.sortSelect}>
              <Select
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
                startAdornment={<SortIcon style={{ marginRight: 8 }} />}
              >
                <MenuItem value="newest">最新发布</MenuItem>
                <MenuItem value="priceAsc">价格从低到高</MenuItem>
                <MenuItem value="priceDesc">价格从高到低</MenuItem>
              </Select>
            </FormControl>
          </div>
        </Paper>

        {loading ? (
          <div className={classes.loadingContainer}>
            <CircularProgress />
          </div>
        ) : items.length === 0 ? (
          <Paper className={classes.noItems}>
            <Typography variant="h6" color="textSecondary">
              暂无商品
            </Typography>
          </Paper>
        ) : (
          <Grid container spacing={3}>
            {items.map((item) => (
              <Grid item xs={12} sm={6} md={4} lg={3} key={item._id}>
                <Card className={classes.itemCard}>
                  <CardMedia
                    className={classes.itemMedia}
                    image={`http://localhost:5000${item.image}`}
                    title={item.title}
                  />
                  <CardContent className={classes.itemContent}>
                    <Typography variant="h6" gutterBottom noWrap>
                      {item.title}
                    </Typography>
                    <Typography 
                      variant="body2" 
                      color="textSecondary"
                      style={{
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        display: '-webkit-box',
                        WebkitLineClamp: 2,
                        WebkitBoxOrient: 'vertical',
                      }}
                    >
                      {item.description}
                    </Typography>
                    <Typography className={classes.price}>
                      ¥ {item.price.toLocaleString()}
                    </Typography>
                  </CardContent>
                  <CardActions className={classes.cardActions}>
                    <Button
                      variant="contained"
                      color="primary"
                      className={classes.buyButton}
                      onClick={() => handleViewItem(item._id)}
                      fullWidth
                    >
                      去购买
                    </Button>
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        )}
      </Container>
    </div>
  );
}

export default Home;