import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  makeStyles,
  Grid,
  Card,
  CardContent,
  CardMedia,
  Box,
  Chip,
  Divider,
  CircularProgress,
  Button,
} from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import ShoppingBasketIcon from '@material-ui/icons/ShoppingBasket';
import LocalOfferIcon from '@material-ui/icons/LocalOffer';
import AccessTimeIcon from '@material-ui/icons/AccessTime';

const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  pageTitle: {
    fontWeight: 600,
    marginBottom: theme.spacing(4),
    display: 'flex',
    alignItems: 'center',
    gap: theme.spacing(1),
    '& svg': {
      color: theme.palette.primary.main,
    },
  },
  card: {
    display: 'flex',
    marginBottom: theme.spacing(2),
    borderRadius: theme.spacing(2),
    boxShadow: '0 2px 8px rgba(0,0,0,0.08)',
    transition: 'all 0.3s ease',
    '&:hover': {
      transform: 'translateY(-4px)',
      boxShadow: '0 8px 16px rgba(0,0,0,0.12)',
    },
  },
  cardMedia: {
    width: 160,
    height: 160,
    objectFit: 'cover',
    borderRadius: theme.spacing(1),
    margin: theme.spacing(2),
  },
  cardContent: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
    padding: theme.spacing(2),
  },
  orderInfo: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'flex-start',
    marginBottom: theme.spacing(2),
  },
  price: {
    color: theme.palette.error.main,
    fontWeight: 700,
    fontSize: '1.25rem',
    display: 'flex',
    alignItems: 'center',
    gap: theme.spacing(0.5),
  },
  priceIcon: {
    fontSize: '1rem',
    color: theme.palette.error.light,
  },
  status: {
    marginLeft: 'auto',
  },
  orderTime: {
    display: 'flex',
    alignItems: 'center',
    gap: theme.spacing(0.5),
    color: theme.palette.text.secondary,
    fontSize: '0.875rem',
    marginTop: theme.spacing(1),
  },
  title: {
    fontWeight: 500,
    marginBottom: theme.spacing(1),
  },
  loadingContainer: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    minHeight: 400,
  },
  emptyContainer: {
    textAlign: 'center',
    padding: theme.spacing(4),
    backgroundColor: theme.palette.background.paper,
    borderRadius: theme.spacing(2),
    marginTop: theme.spacing(4),
  },
  actionButton: {
    marginTop: theme.spacing(2),
  },
}));

function OrderPage() {
  const classes = useStyles();
  const history = useHistory();
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchOrders();
  }, []);

  const fetchOrders = async () => {
    try {
      setLoading(true);
      // 添加token检查
      const token = localStorage.getItem('token');
      console.log('当前token:', token);
  
      const response = await fetch('http://localhost:5000/api/orders', {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
  
      // 添加响应状态检查
      console.log('响应状态:', response.status);
      
      if (!response.ok) {
        throw new Error('获取订单失败');
      }
  
      const data = await response.json();
      console.log('获取到的订单数据:', data);  // 查看完整的返回数据
      setOrders(data);
    } catch (error) {
      console.error('获取订单失败:', error);
      setOrders([]);
    } finally {
      setLoading(false);
    }
  };

  const getStatusChip = (status) => {
    const statusMap = {
      pending: { label: '待付款', color: 'default' },
      paid: { label: '已付款', color: 'primary' },
      shipped: { label: '已发货', color: 'info' },
      completed: { label: '已完成', color: 'success' },
      cancelled: { label: '已取消', color: 'error' },
    };

    const statusInfo = statusMap[status] || { label: '未知状态', color: 'default' };
    return (
      <Chip
      size="small"
      label="已付款"  // 直接显示"已付款"
      color="primary"  // 使用主色调
      className={classes.statusChip}
      />
    );
  };

  const handleItemClick = (itemId) => {
    history.push(`/items/${itemId}`);
  };

  if (loading) {
    return (
      <Container className={classes.loadingContainer}>
        <CircularProgress />
      </Container>
    );
  }

  return (
    <Container className={classes.container}>
      <Typography variant="h5" className={classes.pageTitle}>
        <ShoppingBasketIcon />
        我的订单
      </Typography>

      {loading ? (
      <Box className={classes.loadingContainer}>
        <CircularProgress />
      </Box>
    ) : orders.length > 0 ? (
      orders.map((order) => (
        <Card key={order._id} className={classes.card}>
          {/* 根据商品是否存在显示不同的内容 */}
          {order.item ? (
            // 商品存在时显示正常信息
            <>
              <CardMedia
                className={classes.cardMedia}
                image={order.item.image ? `http://localhost:5000${order.item.image}` : '/default-item-image.jpg'}
                title={order.item.title}
                onClick={() => handleItemClick(order.item._id)}
              />
              <CardContent className={classes.cardContent}>
                <Box className={classes.orderInfo}>
                  <Typography variant="h6" className={classes.title}>
                    {order.item.title}
                  </Typography>
                  <Chip
                    label="已付款"
                    color="primary"
                    size="small"
                    className={classes.statusChip}
                  />
                </Box>
                <Typography variant="body2" color="textSecondary">
                  {order.item.description}
                </Typography>
                <Typography className={classes.price}>
                  <LocalOfferIcon className={classes.priceIcon} />
                  ¥ {order.item.price?.toLocaleString()}
                </Typography>
                <Typography variant="caption" color="textSecondary">
                  下单时间: {new Date(order.createdAt).toLocaleString()}
                </Typography>
              </CardContent>
            </>
          ) : (
            // 商品已删除时显示下架信息
            <CardContent className={classes.cardContent}>
              <Box className={classes.orderInfo}>
                <Typography variant="h6" className={classes.title} style={{ color: '#999' }}>
                  商品已下架
                </Typography>
                <Chip
                  label="已付款"
                  color="primary"
                  size="small"
                  className={classes.statusChip}
                />
              </Box>
              
              <Typography variant="caption" color="textSecondary">
                下单时间: {new Date(order.createdAt).toLocaleString()}
              </Typography>
            </CardContent>
          )}
        </Card>
      ))
    ) : (
      <Box className={classes.emptyContainer}>
        <Typography variant="h6" gutterBottom>
          暂无订单
        </Typography>
        <Typography color="textSecondary" gutterBottom>
          去发现更多好物吧！
        </Typography>
        <Button
          variant="contained"
          color="primary"
          className={classes.actionButton}
          onClick={() => history.push('/')}
        >
          浏览商品
        </Button>
      </Box>
    )}
  </Container>
  );
}

export default OrderPage;