import React, { useState, useEffect } from 'react';
import {
  Container,
  Grid,
  Paper,
  Typography,
  makeStyles,
  Avatar,
  Tabs,
  Tab,
  Button,
  Divider,
  Box,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  CircularProgress,
  Fade,
} from '@material-ui/core';
import { useAuth } from '../../context/AuthContext';
import { useHistory } from 'react-router-dom';
import AccountBalanceWalletIcon from '@material-ui/icons/AccountBalanceWallet';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: '#ffffff',
    minHeight: '100vh',
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  container: {
    maxWidth: 1200,
  },
  profileSection: {
    backgroundColor: '#ffffff',
    borderRadius: 16,
    padding: theme.spacing(4),
    boxShadow: 'none',
    borderBottom: '1px solid #f0f0f0',
  },
  avatar: {
    width: 100,
    height: 100,
    margin: '0 auto',
    marginBottom: theme.spacing(2),
    backgroundColor: theme.palette.primary.main,
    fontSize: '2rem',
  },
  username: {
    fontWeight: 600,
    fontSize: '1.5rem',
    color: '#1a1a1a',
    marginBottom: theme.spacing(0.5),
  },
  email: {
    color: '#666',
    marginBottom: theme.spacing(3),
  },
  balanceSection: {
    backgroundColor: theme.palette.primary.main,
    color: 'white',
    padding: theme.spacing(3),
    borderRadius: 12,
    marginBottom: theme.spacing(3),
  },
  balanceIcon: {
    fontSize: 32,
    marginRight: theme.spacing(1),
  },
  balanceAmount: {
    fontSize: '2rem',
    fontWeight: 600,
    marginTop: theme.spacing(1),
  },
  newItemButton: {
    backgroundColor: theme.palette.primary.main,
    color: 'white',
    padding: theme.spacing(1.5, 3),
    borderRadius: 25,
    fontSize: '0.9rem',
    fontWeight: 600,
    textTransform: 'none',
    '&:hover': {
      backgroundColor: theme.palette.primary.dark,
    },
  },
  contentSection: {
    backgroundColor: '#ffffff',
    borderRadius: 16,
    boxShadow: 'none',
    borderBottom: '1px solid #f0f0f0',
    overflow: 'hidden',
  },
  tabs: {
    '& .MuiTab-root': {
      color: '#666',
      margin: theme.spacing(0, 2),
      fontWeight: 500,
      fontSize: '0.95rem',
      textTransform: 'none',
      '&:hover': {
        color: theme.palette.primary.main,
        backgroundColor: 'transparent',
      },
    },
  },
  itemCard: {
    height: '100%',
    borderRadius: 12,
    overflow: 'hidden',
    backgroundColor: '#ffffff',
    boxShadow: 'none',
    borderBottom: '1px solid #f0f0f0',
    transition: 'transform 0.2s ease-in-out',
    '&:hover': {
      transform: 'translateY(-4px)',
    },
  },
  itemImage: {
    height: 200,
    backgroundSize: 'cover',
    backgroundPosition: 'center',
  },
  itemContent: {
    padding: theme.spacing(2),
  },
  itemTitle: {
    fontSize: '1.1rem',
    fontWeight: 600,
    marginBottom: theme.spacing(1),
    color: '#1a1a1a',
  },
  itemDescription: {
    color: '#666',
    fontSize: '0.9rem',
    marginBottom: theme.spacing(2),
  },
  price: {
    color: theme.palette.primary.main,
    fontSize: '1.25rem',
    fontWeight: 600,
    marginBottom: theme.spacing(1),
  },
  dateText: {
    color: '#666',
    fontSize: '0.85rem',
  },
  itemActions: {
    padding: theme.spacing(1),
    display: 'flex',
    justifyContent: 'flex-end',
    gap: theme.spacing(1),
    borderTop: '1px solid #f0f0f0',
  },
  actionButton: {
    padding: 8,
    color: '#666',
    '&:hover': {
      backgroundColor: 'rgba(0,0,0,0.03)',
    },
  },
  deleteButton: {
    padding: 8,
    color: theme.palette.error.main,
    '&:hover': {
      backgroundColor: 'rgba(0,0,0,0.03)',
    },
  },
  dialog: {
    '& .MuiDialog-paper': {
      borderRadius: 12,
    },
  },
  dialogTitle: {
    backgroundColor: theme.palette.primary.main,
    color: 'white',
  },
  dialogContent: {
    padding: theme.spacing(3),
  },
  dialogActions: {
    padding: theme.spacing(2),
  },
  loadingContainer: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    minHeight: 300,
  },
  noItems: {
    textAlign: 'center',
    padding: theme.spacing(6),
    color: '#666',
  },
  favoriteCard: {
    height: '100%',
    borderRadius: 12,
    overflow: 'hidden',
    backgroundColor: '#ffffff',
    boxShadow: 'none',
    borderBottom: '1px solid #f0f0f0',
    transition: 'transform 0.2s ease-in-out',
    '&:hover': {
      transform: 'translateY(-4px)',
    },
  },
  favoriteButton: {
    color: theme.palette.error.main,
    '&:hover': {
      backgroundColor: 'rgba(0,0,0,0.03)',
    },
  },
  balanceCard: {
    backgroundColor: theme.palette.primary.main,
    color: 'white',
    padding: theme.spacing(2),
    borderRadius: theme.shape.borderRadius,
    marginTop: theme.spacing(2),
    textAlign: 'center',
    cursor: 'pointer',
    '&:hover': {
      backgroundColor: theme.palette.primary.dark,
    },
  },
  container: {
    marginTop: theme.spacing(4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  email: {
    marginTop: theme.spacing(1),
    color: theme.palette.text.secondary,
  },
}));

// ... (前面的 imports 和 useStyles 保持不变) ...

function Profile() {
  const classes = useStyles();
  const { user } = useAuth();
  const history = useHistory();
  const [userItems, setUserItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [tabValue, setTabValue] = useState(0);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [itemToDelete, setItemToDelete] = useState(null);
  const [favorites, setFavorites] = useState([]);
  const [loadingFavorites, setLoadingFavorites] = useState(true);
  const [balance, setBalance] = useState(0);
  useEffect(() => {
    if (user) {
      fetchUserItems();
    }
  }, [user]);
  useEffect(() => {
    fetchUserBalance();
  }, []);
  const fetchUserItems = async () => {
    try {
      const response = await fetch(`http://localhost:5000/api/items/user/${user.id}`);
      const data = await response.json();
      setUserItems(data);
    } catch (error) {
      console.error('Error fetching user items:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  const handleNewItem = () => {
    history.push('/items/new');
  };

  const displayBalance = () => {
    const balance = Number(user?.balance || 0);
    return balance.toLocaleString('zh-CN', {
      minimumFractionDigits: 2,
      maximumFractionDigits: 2
    });
  };

  const handleDeleteClick = (item) => {
    setItemToDelete(item);
    setDeleteDialogOpen(true);
  };

  const handleDeleteConfirm = async () => {
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`http://localhost:5000/api/items/${itemToDelete._id}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      });

      if (response.ok) {
        setUserItems(userItems.filter(item => item._id !== itemToDelete._id));
        fetchUserItems();
      }
    } catch (error) {
      console.error('Delete error:', error);
    } finally {
      setDeleteDialogOpen(false);
      setItemToDelete(null);
    }
  };
 // 获取收藏列表
 const fetchFavorites = async () => {
  try {
    const token = localStorage.getItem('token');
    const response = await fetch(`http://localhost:5000/api/favorites`, {
      headers: {
        'Authorization': `Bearer ${token}`
      }
    });
    const data = await response.json();
    setFavorites(data);
  } catch (error) {
    console.error('Error fetching favorites:', error);
  } finally {
    setLoadingFavorites(false);
  }
};

// 在组件加载时获取收藏列表
useEffect(() => {
  if (user) {
    fetchFavorites();
  }
}, [user]);

// 取消收藏
const handleRemoveFavorite = async (itemId) => {
  try {
    const token = localStorage.getItem('token');
    const response = await fetch(`http://localhost:5000/api/favorites/${itemId}`, {
      method: 'DELETE',
      headers: {
        'Authorization': `Bearer ${token}`
      }
    });

    if (response.ok) {
      setFavorites(favorites.filter(item => item._id !== itemId));
    }
  } catch (error) {
    console.error('Error removing favorite:', error);
  }
};

  if (!user) {
    return (
      <Container className={classes.root}>
        <Typography variant="h5" align="center">
          请先登录以查看个人资料
        </Typography>
      </Container>
    );
  }
  const fetchUserBalance = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/users/balance', {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });
      const data = await response.json();
      if (response.ok) {
        setBalance(data.balance);
      }
    } catch (error) {
      console.error('Error fetching balance:', error);
    }
  };


  return (
    <Box className={classes.root}>
      <Container className={classes.container}>
        <Grid container spacing={4}>
          <Grid item xs={12} md={4}>
            <Fade in timeout={800}>
              <Paper className={classes.profileSection} elevation={0}>
                <Box textAlign="center">
                  <Avatar className={classes.avatar}>
                    {user.username.charAt(0).toUpperCase()}
                  </Avatar>
                  <Typography className={classes.username}>
                    {user.username}
                  </Typography>
                  <Typography className={classes.email}>
                    {user.email}
                  </Typography>

                  <Box className={classes.balanceSection}>
  <Box display="flex" alignItems="center" justifyContent="center">
    <AccountBalanceWalletIcon className={classes.balanceIcon} />
    <Typography variant="h6">
      我的余额
    </Typography>
  </Box>
  <Typography className={classes.balanceAmount}>
    ¥ {balance?.toFixed(2)}
  </Typography>
</Box>

                  <Button
                    variant="contained"
                    className={classes.newItemButton}
                    onClick={handleNewItem}
                    startIcon={<AddIcon />}
                  >
                    发布新商品
                  </Button>
                </Box>
              </Paper>
            </Fade>
          </Grid>

          <Grid item xs={12} md={8}>
            <Fade in timeout={1000}>
              <Paper className={classes.contentSection} elevation={0}>
                <Tabs
                  value={tabValue}
                  onChange={handleTabChange}
                  indicatorColor="primary"
                  textColor="primary"
                  centered
                  className={classes.tabs}
                >
                  <Tab label="我发布的商品" />
                  <Tab label="我的收藏" />
                </Tabs>
                <Divider />

                {tabValue === 0 && (
                  <Box p={3}>
                    {loading ? (
                      <Box className={classes.loadingContainer}>
                        <CircularProgress style={{ color: '#8E37D7' }} />
                      </Box>
                    ) : userItems.length === 0 ? (
                      <Typography className={classes.noItems}>
                        还没有发布任何商品
                      </Typography>
                    ) : (
                      <Grid container spacing={3}>
                        {userItems.map((item) => (
                          <Grid item xs={12} sm={6} key={item._id}>
                            <Paper className={classes.itemCard} elevation={0}>
                              <div
                                className={classes.itemImage}
                                style={{
                                  backgroundImage: `url(http://localhost:5000${item.image})`,
                                }}
                              />
                              <div className={classes.itemContent}>
                                <Typography className={classes.itemTitle}>
                                  {item.title}
                                </Typography>
                                <Typography className={classes.itemDescription}>
                                  {item.description}
                                </Typography>
                                <Typography className={classes.price}>
                                  ¥ {item.price}
                                </Typography>
                                <Typography className={classes.dateText}>
                                  发布于 {new Date(item.createdAt).toLocaleDateString()}
                                </Typography>
                              </div>
                              <div className={classes.itemActions}>
                                <IconButton
                                  className={classes.actionButton}
                                  onClick={() => history.push(`/items/${item._id}/edit`)}
                                  size="small"
                                >
                                  <EditIcon />
                                </IconButton>
                                <IconButton
                                  className={classes.deleteButton}
                                  onClick={() => handleDeleteClick(item)}
                                  size="small"
                                >
                                  <DeleteIcon />
                                </IconButton>
                              </div>
                            </Paper>
                          </Grid>
                        ))}
                      </Grid>
                    )}
                  </Box>
                )}

{tabValue === 1 && (
      <Box p={3}>
        {loadingFavorites ? (
          <Box className={classes.loadingContainer}>
            <CircularProgress style={{ color: '#1976d2' }} />
          </Box>
        ) : favorites.length === 0 ? (
          <Typography className={classes.noItems}>
            还没有收藏任何商品
          </Typography>
        ) : (
          <Grid container spacing={3}>
            {favorites.map((item) => (
              <Grid item xs={12} sm={6} key={item._id}>
                <Paper className={classes.favoriteCard} elevation={0}>
                  <div
                    className={classes.itemImage}
                    style={{
                      backgroundImage: `url(http://localhost:5000${item.image})`,
                    }}
                  />
                  <div className={classes.itemContent}>
                    <Typography className={classes.itemTitle}>
                      {item.title}
                    </Typography>
                    <Typography className={classes.itemDescription}>
                      {item.description}
                    </Typography>
                    <Typography className={classes.price}>
                      ¥ {item.price}
                    </Typography>
                    <Typography className={classes.dateText}>
                      收藏于 {new Date(item.createdAt).toLocaleDateString()}
                    </Typography>
                  </div>
                  <div className={classes.itemActions}>
                    <IconButton
                      className={classes.favoriteButton}
                      onClick={() => handleRemoveFavorite(item._id)}
                      size="small"
                    >
                      <FavoriteIcon />
                    </IconButton>
                  </div>
                </Paper>
              </Grid>
            ))}
          </Grid>
        )}
      </Box>
    )}
              </Paper>
            </Fade>
          </Grid>
        </Grid>

        <Dialog
          open={deleteDialogOpen}
          onClose={() => setDeleteDialogOpen(false)}
          className={classes.dialog}
        >
          <DialogTitle className={classes.dialogTitle}>
            确认删除
          </DialogTitle>
          <DialogContent className={classes.dialogContent}>
            <Typography>
              确定要删除商品 "{itemToDelete?.title}" 吗？此操作不可撤销。
            </Typography>
          </DialogContent>
          <DialogActions className={classes.dialogActions}>
            <Button
              onClick={() => setDeleteDialogOpen(false)}
              variant="outlined"
              style={{ borderRadius: 20 }}
            >
              取消
            </Button>
            <Button
              onClick={handleDeleteConfirm}
              variant="contained"
              color="secondary"
              style={{ borderRadius: 20 }}
            >
              确认删除
            </Button>
          </DialogActions>
        </Dialog>
      </Container>
    </Box>
  );
}

export default Profile;