import React, { useState, useEffect, useRef } from 'react';
import { useAuth } from '../../context/AuthContext';
import {
  Container,
  Paper,
  Typography,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Avatar,
  TextField,
  Button,
  Box,
  makeStyles,
  Badge
} from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  container: {
    marginTop: theme.spacing(4),
    display: 'flex',
    gap: theme.spacing(2),
  },
  userList: {
    width: '30%',
    minWidth: 250,
  },
  chatBox: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
  },
  messageList: {
    flex: 1,
    minHeight: 400,
    maxHeight: 600,
    overflowY: 'auto',
    padding: theme.spacing(2),
    backgroundColor: '#f5f5f5',
  },
  messageInput: {
    padding: theme.spacing(2),
  },
  myMessage: {
    backgroundColor: '#FF7B7B',
    color: 'white',
    padding: '8px 12px',
    borderRadius: '4px',
    display: 'inline-block',
    marginLeft: 'auto',
    marginBottom: '2px',
    fontSize: '14px',
    whiteSpace: 'pre-wrap',
  },
  otherMessage: {
    backgroundColor: 'white',
    color: 'black',
    padding: '8px 12px',
    borderRadius: '4px',
    display: 'inline-block',
    marginRight: 'auto',
    marginBottom: '2px',
    fontSize: '14px',
    whiteSpace: 'pre-wrap',
  },
  messageTime: {
    fontSize: '12px',
    color: '#999',
    marginTop: '4px',
  }
}));

function Messages() {
  const classes = useStyles();
  const { user } = useAuth();
  const messagesEndRef = useRef(null);
  const [users, setUsers] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [unreadCounts,setUnreadMessages] = useState({});

  // 获取用户列表
  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const response = await fetch('http://localhost:5000/api/users', {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        });
        const data = await response.json();
        setUsers(data.filter(u => u._id !== user.id));
      } catch (error) {
        console.error('Error:', error);
      }
    };
    fetchUsers();
  }, [user.id]);

  // 在选择用户时标记消息为已读
useEffect(() => {
    if (selectedUser) {
      // 获取消息
      const fetchMessages = async () => {
        try {
          const response = await fetch(`http://localhost:5000/api/messages/${selectedUser._id}`, {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`
            }
          });
          const data = await response.json();
          setMessages(data);
          
          // 标记为已读
          await fetch(`http://localhost:5000/api/messages/read/${selectedUser._id}`, {
            method: 'PUT',
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`
            }
          });
        } catch (error) {
          console.error('Error:', error);
        }
      };
      fetchMessages();
    }
  }, [selectedUser]);

  // 发送消息
  const handleSendMessage = async () => {
    if (!newMessage.trim() || !selectedUser) return;

    try {
      const response = await fetch('http://localhost:5000/api/messages', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        },
        body: JSON.stringify({
          receiverId: selectedUser._id,
          content: newMessage.trim()
        })
      });

      const data = await response.json();
      
      if (response.ok) {
        setMessages(prevMessages => [...prevMessages, data]);
        setNewMessage('');
      } else {
        alert(data.message || '发送失败');
      }
    } catch (error) {
      console.error('Error:', error);
      alert('发送失败，请重试');
    }
  };

  return (
    <Container className={classes.container}>
      {/* 用户列表 */}
      <Paper className={classes.userList}>
        <List>
          {users.map((u) => (
            <ListItem 
              button 
              key={u._id}
              selected={selectedUser?._id === u._id}
              onClick={() => setSelectedUser(u)}
            >
              <ListItemAvatar>
              <Badge 
      badgeContent={unreadCounts[u._id] > 0 ? unreadCounts[u._id] : null}
      color="secondary"
      overlap="circular"
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
    >
                <Avatar>{u.username[0]}</Avatar>
                </Badge>
              </ListItemAvatar>
             
              <ListItemText 
    primary={
      <Box display="flex" alignItems="center">
        <Typography>{u.username}</Typography>
        {unreadCounts[u._id] > 0 && (
          <Box 
            ml={1} 
            bgcolor="secondary.main" 
            color="white" 
            px={1} 
            borderRadius="10px"
            fontSize="12px"
          >
            {unreadCounts[u._id]}
          </Box>
        )}
      </Box>
    }
  />
</ListItem>
          ))}
        </List>
      </Paper>

      {/* 聊天区域 */}
      <Paper className={classes.chatBox}>
        {selectedUser ? (
          <>
            <Box p={2}>
              <Typography variant="h6">
                与 {selectedUser.username} 的对话
              </Typography>
            </Box>
            <Box className={classes.messageList}>
            {messages.map((message) => {
    // 确保正确判断消息发送者
    const isMyMessage = message.sender === user.id || message.sender?._id === user.id;
    
    return (
      <Box
        key={message._id}
        style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: isMyMessage ? 'flex-end' : 'flex-start',
        }}
      >
        <Box
          className={isMyMessage ? classes.myMessage : classes.otherMessage}
        >
          {message.content}
        </Box>
        <Typography 
          className={classes.messageTime}
          style={{
            alignSelf: isMyMessage ? 'flex-end' : 'flex-start'
          }}
        >
          {new Date(message.createdAt).toLocaleString()}
        </Typography>
      </Box>
    );
  })}
              <div ref={messagesEndRef} />
            </Box>
            <Box className={classes.messageInput}>
              <TextField
                fullWidth
                multiline
                rows={2}
                variant="outlined"
                placeholder="输入消息..."
                value={newMessage}
                onChange={(e) => setNewMessage(e.target.value)}
                onKeyPress={(e) => {
                  if (e.key === 'Enter' && !e.shiftKey) {
                    e.preventDefault();
                    handleSendMessage();
                  }
                }}
              />
              <Box mt={1} display="flex" justifyContent="flex-end">
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleSendMessage}
                  disabled={!newMessage.trim()}
                >
                  发送
                </Button>
              </Box>
            </Box>
          </>
        ) : (
          <Box p={4} textAlign="center">
            <Typography color="textSecondary">
              选择一个用户开始聊天
            </Typography>
          </Box>
        )}
      </Paper>
    </Container>
  );
}

export default Messages;