import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Button,
  makeStyles,
  Paper,
  Grid,
  CircularProgress,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
} from '@material-ui/core';
import { useParams, useHistory } from 'react-router-dom';
import { useAuth } from '../../context/AuthContext';
import EditIcon from '@material-ui/icons/Edit';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';

const useStyles = makeStyles((theme) => ({
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(3),
  },
  image: {
    width: '100%',
    height: 'auto',
    maxHeight: '500px',
    objectFit: 'contain',
  },
  price: {
    color: theme.palette.primary.main,
    fontWeight: 600,
    fontSize: '2rem',
    marginTop: theme.spacing(2),
  },
  description: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    whiteSpace: 'pre-wrap',
  },
  seller: {
    marginTop: theme.spacing(2),
    color: theme.palette.text.secondary,
  },
  buttonGroup: {
    marginTop: theme.spacing(3),
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  loadingContainer: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    minHeight: '60vh',
  },
}));

function ItemDetail() {
  const classes = useStyles();
  const { id } = useParams();
  const history = useHistory();
  const { user } = useAuth();
  const [item, setItem] = useState(null);
  const [loading, setLoading] = useState(true);
  const [isFavorite, setIsFavorite] = useState(false);
  const [isEditing, setIsEditing] = useState(false);
  const [editFormData, setEditFormData] = useState({
    title: '',
    description: '',
    price: '',
  });

  useEffect(() => {
    fetchItem();
    if (user) {
      checkFavorite();
    }
  }, [id, user]);

  useEffect(() => {
    if (item) {
      setEditFormData({
        title: item.title,
        description: item.description || '',
        price: item.price,
      });
    }
  }, [item]);
// 添加联系卖家的函数
const handleContactSeller = () => {
  if (!user) {
    alert('请先登录');
    return;
  }
  // 跳转到消息页面并传递卖家信息
  history.push('/messages', { 
    selectedUser: item.seller 
  });
};
  const fetchItem = async () => {
    try {
      const response = await fetch(`http://localhost:5000/api/items/${id}`);
      if (!response.ok) {
        throw new Error('商品不存在');
      }
      const data = await response.json();
      setItem(data);
    } catch (error) {
      console.error('Error fetching item:', error);
    } finally {
      setLoading(false);
    }
  };

  const checkFavorite = async () => {
    try {
      const response = await fetch(`http://localhost:5000/api/favorites/check/${id}`, {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });
      const data = await response.json();
      setIsFavorite(data.isFavorite);
    } catch (error) {
      console.error('Error checking favorite status:', error);
    }
  };

  const handleFavorite = async () => {
    if (!user) {
      history.push('/login');
      return;
    }

    try {
      const method = isFavorite ? 'DELETE' : 'POST';
      const response = await fetch(`http://localhost:5000/api/favorites/${id}`, {
        method,
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        }
      });

      if (response.ok) {
        setIsFavorite(!isFavorite);
      }
    } catch (error) {
      console.error('Error toggling favorite:', error);
    }
  };

  const handleAddToCart = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/cart/add', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        },
        body: JSON.stringify({ itemId: item._id })
      });
  
      const data = await response.json();
      
      if (!response.ok) {
        throw new Error(data.message || '添加失败');
      }
  
      alert('成功添加到购物车！');
    } catch (error) {
      console.error('Error adding to cart:', error);
      alert(error.message || '添加失败，请重试');
    }
  };

  const handleSaveEdit = async () => {
    try {
      const response = await fetch(`http://localhost:5000/api/items/${item._id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        },
        body: JSON.stringify(editFormData)
      });

      if (!response.ok) {
        throw new Error('更新失败');
      }

      const updatedItem = await response.json();
      setItem(updatedItem);
      setIsEditing(false);
    } catch (error) {
      console.error('Error updating item:', error);
      alert('更新失败，请重试');
    }
  };
// 添加购买处理函数
const handlePurchase = async () => {
  try {
    console.log('正在创建订单 - 商品ID:', item._id);
    
    const response = await fetch('http://localhost:5000/api/orders', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${localStorage.getItem('token')}`
      },
      body: JSON.stringify({
        itemId: item._id
      })
    });

    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.message || '购买失败');
    }

    const data = await response.json();
    console.log('订单创建成功:', data);
    alert('购买成功！');
    history.push('/my-items');
  } catch (error) {
    console.error('购买失败:', error);
    alert('购买失败，请重试');
  }
};


  if (loading) {
    return (
      <Container className={classes.loadingContainer}>
        <CircularProgress />
      </Container>
    );
  }

  if (!item) {
    return (
      <Container>
        <Typography variant="h5">商品不存在</Typography>
      </Container>
    );
  }

  return (
    <Container className={classes.container}>
      <Paper className={classes.paper}>
        <Grid container spacing={4}>
          <Grid item xs={12} md={6}>
            <img src={`http://localhost:5000${item.image}`} alt={item.title} className={classes.image} />
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography variant="h4" component="h1">
              {item.title}
            </Typography>
            <Typography className={classes.price}>
              ¥ {item.price?.toLocaleString()}
            </Typography>
            <Typography className={classes.description} variant="body1">
              {item.description}
            </Typography>
            <Typography className={classes.seller}>
              卖家：{item.seller.username}
            </Typography>
            <div className={classes.buttonGroup}>
              {user && user.id === item.seller._id ? (
                <Button
                  variant="contained"
                  color="primary"
                  startIcon={<EditIcon />}
                  onClick={() => setIsEditing(true)}
                >
                  编辑商品
                </Button>
              ) : (
                <>
                <Button
  variant="contained"
  color="primary"
  onClick={handlePurchase}
  disabled={!user || user.id === item.seller._id}
  style={{ backgroundColor: '#ff4081' }}
>
  立即购买
</Button>
                  <Button
                    variant="contained"
                    color="primary"
                    startIcon={<ShoppingCartIcon />}
                    onClick={handleAddToCart}
                  >
                    加入购物车
                  </Button>
                  <Button
          variant="outlined"
          color="primary"
          onClick={handleContactSeller}
          disabled={!user || user.id === item.seller._id}
        >
          联系卖家
        </Button>
                  <Button
                    variant="outlined"
                    color="primary"
                    startIcon={isFavorite ? <FavoriteIcon /> : <FavoriteBorderIcon />}
                    onClick={handleFavorite}
                  >
                    {isFavorite ? '取消收藏' : '收藏'}
                  </Button>
                 
                </>
              )}
            </div>
          </Grid>
        </Grid>
      </Paper>

      {/* 编辑对话框 */}
      <Dialog 
        open={isEditing} 
        onClose={() => setIsEditing(false)}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>编辑商品信息</DialogTitle>
        <DialogContent>
          <TextField
            name="title"
            label="商品名称"
            value={editFormData.title}
            onChange={(e) => setEditFormData({...editFormData, title: e.target.value})}
            fullWidth
            margin="normal"
          />
          <TextField
            name="description"
            label="商品描述"
            value={editFormData.description}
            onChange={(e) => setEditFormData({...editFormData, description: e.target.value})}
            fullWidth
            multiline
            rows={4}
            margin="normal"
          />
          <TextField
            name="price"
            label="价格"
            value={editFormData.price}
            onChange={(e) => setEditFormData({...editFormData, price: e.target.value})}
            fullWidth
            type="number"
            margin="normal"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setIsEditing(false)}>
            取消
          </Button>
          <Button onClick={handleSaveEdit} color="primary" variant="contained">
            保存
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
}

export default ItemDetail;